.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-weight: bold;
  font-display: auto;
  font-family: monospace, monospace;

  img {
    height: 44px;
  }

  &__mark {
    margin-right: 5px;
    color: #3d7b36;

    &__home {
      color: #6d96e2;
    }
  }

  &__text {
    font-size: 1.125rem;
    @media #{$media-size-tablet} {
      font-size: 0.9rem;
    }
  }

  &__cursor {
    display: inline-block;
    width: 10px;
    height: 1rem;
    background: #3d7b36;
    margin-left: 5px;
    border-radius: 1px;
    animation: cursor 2s infinite;
  }

  @media (prefers-reduced-motion: reduce) {
    &__cursor {
      animation: none;
    }
  }

}

@keyframes cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
